          <router-link :to="`/detail?id=${item.vod_id}`" class="vod_a" target="_blank">
          <div style="height: 235px; min-width: 168px">
             <img class="videoimg" :src="item.vod_pic" :alt="item.vod_name" :title="item.vod_name">
