<script>
export default {
  name: "ListVideo",
  data() {
    return {
      showList:[],
    };
  },
  props:['List','LieShu'],
  methods: {

  },
  computed:{
    Show(){
      let kong=[];
      let thatlist=this.List
      let num1=Math.ceil(thatlist.length/7)
      let num2=0;
      
      for(let i=0; i<num1;i++){
          //  console.log("i:",i,"num2",num2);
          kong[i]=thatlist.slice( num2,num2+7);
          num2+=7;
      }
      return kong;
    }
  }


};
</script>

