<template>
  <div>
    <!-- <p>电影类别----{{ routername }}</p> -->
    <p>&nbsp;</p>
    <div class="list-box" >
      <dl class="c1">
        <dt>类型 :</dt>
        <a href="javascript:;" v-for="(item,index) in typeList" :key="index" :class="isActiveUrl[0]==item?'current1':''">
          <span @click="routerPath(item,0)">{{item}}</span>
          </a>
      </dl>
      <dl class="c2">
        <dt>年代 :</dt>
        <a href="javascript:;" v-for="item in typeYear" :key="item" :class="isActiveUrl[1]==item?'current2':''">
          <span @click="routerPath(item,1)">{{item}}</span>
          </a>
      </dl>
      <dl class="c3">
        <dt>地区 :</dt>
        <a href="javascript:;" v-for="item in areaList" :key="item" :class="isActiveUrl[2]==item?'current3':''">
          <span @click="routerPath(item,2)">{{item}}</span>
          </a>
      </dl>
      <dl class="c4">
        <dt>语言 :</dt>
        <a href="javascript:;" v-for="item in langList" :key="item" :class="isActiveUrl[3]==item?'current4':''">
          <span @click="routerPath(item,3)">{{item}}</span>
          </a>
      </dl>
      <div class="sort">
        <dt>排序 :</dt>
        <a href="javascript:;" :class="isActiveUrl[4]=='time'?'current5':''"><span @click="routerPath('time',4)">按时间</span></a>
        <a href="javascript:;" :class="isActiveUrl[4]=='hot'?'current5':''"><span @click="routerPath('hot',4)">按人气</span></a>
        <a href="javascript:;" :class="isActiveUrl[4]=='score'?'current5':''"><span @click="routerPath('score',4)">按评分</span></a>
      </div>
    </div>

    <ListVideo :List="mainList" :LieShu="6"></ListVideo>

    <div class="pagination">
        <el-pagination v-if="isPagenew"
            @current-change="pageChange"
            background
            layout="pager"
            :pager-count="5"
            :page-size="40" 
            :total="total">
            </el-pagination>
    </div>
  </div>
</template>

<script>
import service from '../../utils/request';
import ListVideo from "./ListVideo.vue";
export default {
  name: "TypeVideo",
  components: { ListVideo },
  data() {
    return {
      isActiveUrl:['全部','全部','全部','全部','time'],
      total:42,//总的条数
      currentPage:1,
      isPagenew:true,
      typeList:['全部',],
      typeListMv:['全部','科幻','剧情','动作','喜剧','悬疑','爱情','惊悚','冒险','恐怖','动画','犯罪','奇幻','灾难','战争','武侠','奇幻','纪录'],
      typeListTv:['全部','国产','港台','日韩','欧美','喜剧','爱情','音乐','奇幻','战争','犯罪','悬疑','古装','武侠','家庭','短片','校园','文艺','运动','青春','励志','人性','美食','女性','治愈','真人秀'],
      typeListAC:['全部','剧情','萌系','科幻','日常','战斗','战争','热血','机战','游戏','搞笑','恋爱','后宫','百合','基腐','冒险','奇幻','犯罪','悬疑','古装','武侠','泡面','校园','运动','体育','青春','美食','治愈','致郁','励志'],
      typeYear:['全部','2021','2020','2019','2018','2017','2016','2015','2014','2013','2012','2011','2010' ],
      areaList:['全部','中国大陆','中国香港','中国台湾','美国','法国','英国','日本','韩国','德国','印度','西班牙'],
      langList:['全部','国语','英语','粤语','闽南语','韩语','日语','法语','德语','德国','其他'],
      mainList: [],
      routername: "",
    };
  },
  methods: {
    routerPath(path,num){
      this.isActiveUrl[num]=path;
      this.$forceUpdate();
      this.getmainList();
      // console.log("输出",this.isActiveUrl);
    },
    pageChange(val){
      this.currentPage=val;
      // console.log("点击page",this.currentPage,val);
      this.getmainList();
    },
    gettypeList(val) {
      this.typeList=val;
      //获取类型列表
    },
    getmainList() {// 获得mv电影
    
      var getUrl=this.routername;
      let typecheck=JSON.stringify(this.isActiveUrl);
      service.get("/api/"+getUrl,{// console.log("地址",getUrl);
        params:{
          page:this.currentPage,
          type:typecheck,
        }
      }).then((result) => {
        // console.log("发起请求",this.routername,result.data[0]);
        this.mainList=result.data[0];
        this.total=result.data[1][0].total;
        // console.log("获得页面显示内容总数",result.data[1][0].total);
      }).catch((err) => {
        console.log(err);
      });
    },
    titleName(title){
      // console.log(title);
      this.pageNew();//刷新页码
      if(title=="mv"){document.title="电影";this.gettypeList(this.typeListMv);}
      if(title=="tv"){document.title="电视剧";this.gettypeList(this.typeListTv);}
      if(title=="va"){document.title="综艺";this.gettypeList(this.typeListTv);}
      if(title=="ac"){document.title="动漫";this.gettypeList(this.typeListAC);}
      if(title=="doc"){document.title="纪录片";this.gettypeList(this.typeListTv);}
      
    },
    pageNew(){
      setTimeout(() => { 
        this.isPagenew=!this.isPagenew;
      }, 0);
    }
  },
  watch: {
    $route: function () {
      this.currentPage=1;//初始化页数
      this.isPagenew=false; //初始化为不显示  
      this.routername = this.$route.name;
      this.getmainList();
      this.titleName(this.routername);
    },
  },

  mounted() {
    this.routername = this.$route.name;  
    this.titleName(this.routername) //初始化标题名
    this.currentPage=1; //初始化页数
    this.isPagenew=false; //初始化为不显示  
    this.getmainList(); //加载电影显示列表
  },
};
</script>

<style scoped>
.list-box {
  
  margin-bottom: 15px;
  width: 81vw;
  margin: 0 auto;
}
.list-box dl {
  padding: 5px 0px;
  border-bottom: 1px solid var(--br);
}
.list-box a {
  text-decoration: none;
  line-height: 28px;
  height: 28px;
  text-align: center;
  padding: 0px 7px;
  margin-top: 2px;
  font-size: 13px;
}
.list-box dt {
  font-size: 14px;
  float: left;
  width: 40px;
  height: 30px;
  line-height: 27px;
}

.c1,
.c2,
.c3,
.c4 {
  margin-bottom: 5px;
}
.current1 {
  background: #ff8f00;
  color: #fff;
  border-radius: 3px;
}
.current2 {
  background: #05a7e1;
  color: #fff;
  border-radius: 3px;
}
.current3 {
  background: #ff4949;
  color: #fff;
  border-radius: 3px;
}
.current4 {
  background: #29a681;
  color: #fff;
  border-radius: 3px;
}
.current5 {
  background: #1bdef8;
  color: #fff;
  border-radius: 3px;
}
.pagination{
   margin-top: 20px;
   margin-bottom: 20px;
   display: flex;
   justify-content:center;
}
</style>